@import '../../components/styles/sass/variables.scss';

.skill {
    width: 250px;
    margin: 5px;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin:30px;
}

.icon {
    width: 64px;
    height: 64px;
}
.title{
    color: $miniTitle;
    font-size: 24px;
    font-weight: 500;
    text-transform: uppercase;
}
.discription {
    color: $textColor;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: normal;
    font-family: "Poppins",sans-serif;
    margin: 0;

}